import React, { useState } from "react";
import Scroll from "./Scroll";
import SearchList from "./SearchList";

function Search({ details, 가져온맵 }) {
  const [searchField, setSearchField] = useState("");
  const [searchShow, setSearchShow] = useState(false);

  const filteredCenterd = details.filter((eduCenter) => {
    return (
      eduCenter.centerName.toLowerCase().includes(searchField.toLowerCase()) ||
      eduCenter.address1.toLowerCase().includes(searchField.toLowerCase()) ||
      eduCenter.address2.toLowerCase().includes(searchField.toLowerCase())
    );
  });

  const handleChange = (e) => {
    setSearchField(e.target.value);
    setSearchShow(true);
  };

  function searchList() {
    if (searchShow) {
      return (
        <Scroll>
          <SearchList 가져온맵={가져온맵} filteredCenterd={filteredCenterd} />
        </Scroll>
      );
    }
  }

  return (
    <section>
      <div>
        <input
          type="search"
          placeholder="센터명, 주소 검색"
          onChange={handleChange}
        />
      </div>
      {searchList()}
    </section>
  );
}

export default Search;
