/* global kakao */
import React from "react";
import $ from "jquery";
function Card({ center, 가져온맵 }) {
  return (
    <li>
      <a
        href="#none"
        onClick={() => {
          const moveLatLon = new kakao.maps.LatLng(center.lat, center.lng);

          가져온맵.setLevel(6);
          가져온맵.panTo(moveLatLon);

          const closeOverlay = () => {
            mapCustomOverlay.setMap(null);
          };

          const $wrap = $('<div class="overlay_info" />');
          const $title = $('<div class="title" />').text(center.centerName);
          const $close = $('<div class="btn-close" title="닫기" />')
            .text("X")
            .click(closeOverlay);
          const $body = $('<div class="desc" />');
          const $address = $('<span class="address" />').text(
            center.address1 + center.address2
          );

          $wrap.append($title).append($body);
          $body.append($address);
          $title.append($close);

          const content = $wrap[0];

          const mapCustomOverlay = new kakao.maps.CustomOverlay({
            position: moveLatLon,
            content: content,
            xAnchor: 0.5,
            yAnchor: 1.3,
          });

          mapCustomOverlay.setMap(가져온맵);

          kakao.maps.event.addListener(가져온맵, "zoom_changed", function () {
            const level = 가져온맵.getLevel();

            if (level > 4) {
              mapCustomOverlay.setMap(null);
            } else {
              mapCustomOverlay.setMap(가져온맵);
            }
          });
        }}
      >
        <strong>{center.centerName}</strong>
        <br></br>
        {center.address1}
        {center.address2}
      </a>
    </li>
  );
}

export default Card;
