/* global kakao */
import React, { useEffect, useState, useRef } from "react";
import $ from "jquery";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faCrosshairs } from "@fortawesome/free-solid-svg-icons";

export default function KakaoMap(props) {
  const [kakaoMap, setKakaoMap] = useState(null);
  const container = useRef();
  const btnCurrentTarget = useRef();

  //카카오맵
  useEffect(() => {
    const script = document.createElement("script");
    script.src =
      "https://dapi.kakao.com/v2/maps/sdk.js?appkey=9d8db804e25748be27d2b3c271de3292&libraries=services,clusterer&autoload=false";
    document.head.appendChild(script);

    script.onload = () => {
      kakao.maps.load(() => {
        const center = new kakao.maps.LatLng(36.503603, 127.250751);
        const options = {
          center,
          level: 12,
        };
        const map = new kakao.maps.Map(container.current, options);
        setKakaoMap(map);
        props.가져온맵변경(map);
      });
    };
  }, [container]);

  //줌컨트롤
  useEffect(() => {
    if (kakaoMap === null) {
      return;
    }

    const zoomControl = new kakao.maps.ZoomControl();
    kakaoMap.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
  }, [kakaoMap]);

  // 클러스터러 & 마커
  useEffect(() => {
    if (kakaoMap === null) {
      return;
    }

    const clusterer = new kakao.maps.MarkerClusterer({
      map: kakaoMap,
      averageCenter: true,
      minLevel: 10,
    });

    const markers = props.centerInfo.map(function (pos) {
      const coords = new kakao.maps.LatLng(pos.lat, pos.lng);

      const marker = new kakao.maps.Marker({
        map: kakaoMap,
        position: coords,
      });

      const closeOverlay = () => {
        mapCustomOverlay.setMap(null);
      };

      const $wrap = $('<div class="overlay_info" />');
      const $title = $('<div class="title" />').text(pos.centerName);
      const $close = $('<div class="btn-close" title="닫기" />')
        .text("X")
        .click(closeOverlay);
      const $body = $('<div class="desc" />');
      const $address = $('<span class="address" />').text(
        pos.address1 + pos.address2
      );

      $wrap.append($title).append($body);
      $body.append($address);
      $title.append($close);

      const content = $wrap[0];

      // 커스텀 오버레이를 생성합니다
      const mapCustomOverlay = new kakao.maps.CustomOverlay({
        position: marker.getPosition(),
        content: content,
        xAnchor: 0.5,
        yAnchor: 1.3,
      });

      kakao.maps.event.addListener(marker, "click", function () {
        mapCustomOverlay.setMap(kakaoMap);
        kakaoMap.setCenter(coords);
        kakaoMap.setLevel(6);
      });

      return marker;
    });

    clusterer.addMarkers(markers);
  }, [kakaoMap, props.centerInfo]);

  // 접속위치
  useEffect(() => {
    if (kakaoMap === null) {
      return;
    }

    function currentLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(function (position) {
          const lat = position.coords.latitude;
          const lon = position.coords.longitude;
          const locPosition = new kakao.maps.LatLng(lat, lon);
          displayMarker(locPosition);
        });
      } else {
        const locPosition = new kakao.maps.LatLng(33.450701, 126.570667);
        displayMarker(locPosition);
      }

      function displayMarker(locPosition) {
        var imageSrc = "https://ssl.pstatic.net/static/maps/m/pin_rd.png";
        var imageSize = new kakao.maps.Size(14, 14);
        var imageOption = { offset: new kakao.maps.Point(14, 14) };

        var markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imageOption
        );

        const marker = new kakao.maps.Marker({
          map: kakaoMap,
          image: markerImage,
          position: locPosition,
        });
        kakaoMap.setLevel(4);
        kakaoMap.setCenter(locPosition);
        marker.setMap(kakaoMap);
      }
    }

    //버튼 한번만 누르게 만드는 함수
    function one(dom, event, callback) {
      function handler(e) {
        callback.call(this, e);
        this.removeEventListener(event, handler);
      }
      dom.addEventListener(event, handler);
    }

    one(btnCurrentTarget.current, "click", currentLocation);
  }, [kakaoMap, btnCurrentTarget]);

  return (
    <>
      <Nav setModal={props.setModal} btnCurrentTarget={btnCurrentTarget} />
      <div id="centerMap" ref={container}></div>
    </>
  );
}

function Nav(props) {
  return (
    <nav>
      <button
        ref={props.btnCurrentTarget}
        type="button"
        className="btn btn-current-location"
      >
        <FontAwesomeIcon icon={faCrosshairs} size="2x" />
        <span className="a11y-hidden">현재 위치검색</span>
      </button>
      <button
        type="button"
        onClick={() => {
          props.setModal(true);
        }}
        className="btn btn-edu-search"
      >
        <FontAwesomeIcon icon={faSearch} size="2x" />
        <span className="a11y-hidden">센터검색</span>
      </button>
    </nav>
  );
}
