import React from "react";
import Card from "./Card";

function SearchList({ filteredCenterd, 가져온맵 }) {
  const filtered = filteredCenterd.map((center, i) => (
    <Card key={i} 가져온맵={가져온맵} center={center} />
  ));
  return <ul className="search-list">{filtered}</ul>;
}

export default SearchList;
